@font-face {
	font-family: "icomoon";
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'),
		 url(data:application/font-woff;charset=utf-8;base64,) format('woff');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
	font-family: "icomoon";
	line-height: 1;
	
	/* Add 1px margin-top to center the icon */
	display: block;
	margin-top: 1px;
	
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-twitter:before     { content: "\e602"; }
.icon-facebook:before    { content: "\e603"; }
.icon-github:before      { content: "\e600"; }
.icon-pinterest:before   { content: "\e608"; }
.icon-google-plus:before { content: "\e604"; }
.icon-linkedin:before    { content: "\e601"; }
.icon-youtube:before     { content: "\e60b"; }
.icon-instagram:before   { content: "\e609"; }
.icon-dribbble:before    { content: "\e605"; }
.icon-behance:before     { content: "\e607"; }
.icon-soundcloud:before  { content: "\e606"; }
.icon-vine:before        { content: "\e60a"; } 
